@mixin standard-flex(){
display: flex;
justify-content: space-around;
align-items: center;
}
@mixin text-shadow($color, $px){
    $px2: $px * -1; 
    text-shadow:
    $px $px 2px $color,
    $px2 $px 2px $color,
    $px2 $px2 0 $color,
    $px $px2 0 $color;
}
@keyframes backgroundmotion {
  0%{
    background-position: 0% 0%;
  }
  100%{
background-position: 2000% 2000%;
  }
}
.App {

}
.main{
  overflow: hidden;
  position: relative;
  top: 7vh;
  background-image: url("../public/assets/background.png");
  width: 100%;
  height: 200vh;
  animation: infinite backgroundmotion 300s;
  &--full{
    
    height: 90vh;
  }
}
.navbar{
  position: fixed;
  width: 100%;
  z-index: 9999;
  background: linear-gradient(cadetblue,rgb(138, 170, 255));
  @include standard-flex();

}
.navtext{
  font-size: 1.7vw;
  width: 15%;
  text-align: center;
  cursor: pointer;
  @include text-shadow(black,2px);
  color: white;
  padding: 1%;
  border-radius: 10px;
  &:hover{
    background: rgba(255, 255, 255, 0.308);
  }
}
.footer{
  width: 100%;
  height: 10vh;
  background: linear-gradient(rgb(138, 170, 255),cadetblue);
position: relative;
  z-index: 9999;

  @include standard-flex();
}
.contact{
  @include text-shadow(black,2px);
  font-size: 1.2vw;
  color: gold;
  font-weight: bold;
}
.frame{
  width: 90%;
  height: 90%;
  background: rgba(0, 0, 0, 0.425);
  box-shadow: 0 0 25px black;
  @include standard-flex();
  flex-direction: column;
}
.frbody{
  @include standard-flex();
  width: 100%;
  height: 90vh;
  
}

.langSwitches{
  width: 40%;
  height: 20%;
  // background: gray;
  // border: 2px solid black;
  @include standard-flex();
}

.langSwitch{

  width: 45%;
  height: 95%;
  background: brown;
  box-shadow: 0 0 5px 5px rgb(0, 255, 76);
  cursor: pointer;
  &--english{
    background: url("https://upload.wikimedia.org/wikipedia/commons/thumb/8/83/Flag_of_the_United_Kingdom_%283-5%29.svg/2560px-Flag_of_the_United_Kingdom_%283-5%29.svg.png") center/cover no-repeat;
  }
  &--polish{
    background: url("https://upload.wikimedia.org/wikipedia/en/1/12/Flag_of_Poland.svg") center/cover no-repeat;
  }
  &--unactive{
    box-shadow: 0 0 3px 3px black;
    filter: grayscale(.6);
  }
  &:hover{
    opacity: .5;
  }
}

.portfolioD{
  @include standard-flex();
  flex-direction: column;
  width: 40%;
  background: rgba(0, 0, 0, 0.473);
  font-size: 1.2vw;
  color: gold;
  padding: 2%;
  box-shadow: 0 0 4px black;
  height: 60%;
  &--smaller{
    height: 20%;
  }
}
.portfolioDesc{
  width: 100%;
  height: 90%;
}
.portfolioH1{
  font-size: 1.5vw;
  margin-bottom: 1%;
  height: 10%;
  color: rgb(238, 255, 0);
  @include text-shadow(black, 2px);
  font-weight: bold;
  &--smaller{
    margin-bottom: 5%;
  }
}

.gifPic{
  width: 40%;
  height: 50%;
  box-shadow: 0 0 5px 5px black;
  &--transitions{
    background: url('../public/assets/wideo1.gif') center/cover no-repeat;
  }
  &--elements{
    background: url('../public/assets/wideo2.gif') center/cover no-repeat;
  }
  &--filters{
    background: url('../public/assets/wideo3.gif') center/cover no-repeat;
  }
  &--camera{
    background: url('../public/assets/wideo4.gif') center/cover no-repeat;
  }
}

.legend{
width: 40%;
@include standard-flex();
}

.lcolor{
  width: 33%;
  height: 100%;
  background: rgba(255, 255, 255, 0.308);
  @include standard-flex();
  padding: 1%;
  border-radius: 10px;
  font-weight: bold;
  box-shadow: 0 0 3px 2px black;
  & > div{
  
      &:nth-of-type(1){
      
        border-radius: 100%;
   
        width: 15%;
        height: 100%;
      }  
  }
}

.multiarea{

width: 50%;
height: 40%;
background: rgba(0, 0, 0, 0.473);
@include standard-flex();
flex-direction: column;
}

.maName{
color: gold;
font-size: 1.3vw;
}
.picture{
  width: 80%;
  height: 90%;
  background-size: 80%;
  background-position: center;
  background-repeat: no-repeat;
  filter: drop-shadow(0 0 4px black);
  transition: .6s ease-in;
  opacity: 1;
  transform: translate(0%,0%) rotate(0deg) scale(1);
  &--1{
    opacity: .5;
    transform: translate(-50%,-50%) rotate(1020deg) scale(.8);
  }
  &--3{

    transform: translate(0%,0%) rotate(0deg) scale(1.2);
  }
  &--4{
    opacity: .5;
    transform: translate(100%,0%) rotate(0deg) scale(.8);
  }
  &--5{
    opacity: .5;
    transform: translate(0%,100%) rotate(0deg) scale(.8) scaleX(-1);
    
  }
  &--6{
    opacity: .5;
    background-size: 10%;
    
  }

}
.picDesc{
  position: absolute;
  color: gold;
  top: 50%;
  left: -8%;
  width: 15%;
  height: 30%;
  transform: translateY(-50%);
  background: rgba(0, 0, 0, 0.5);
  @include standard-flex();
  padding: 1%;
}
.maElements{
  width: 80%;
  height: 80%;
  display: flex;
  flex-wrap: wrap;
  align-items: baseline;
  justify-content: center;
  align-content: flex-start;
}
.maElem{
  background: rgba(255, 255, 255, 0.514);
  border-radius: 5px; 
  padding: 2%;  
  margin: 1%;
  font-weight: bold;
  cursor: pointer;
  box-shadow: 0 0 4px 2px black;
  text-decoration: none;
  color: black;
  transition: .1s;
  &:hover{
    transform: translateY(5%);
    background: rgba(255, 255, 255, 0.836);
  }

}